<template>
  <el-dialog title="" :visible.sync="dialogFormAddSubjectVisible"  :show-close="false">
    <el-form :model="form" disabled>
      <el-form-item label="">
        <el-input v-model="form.wantedName"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.intention"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.major"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.address"></el-input>
      </el-form-item>
      <el-form-item label="/">
        <el-input v-model="form.salary"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.jianli"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.phone"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.mailbox"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="handleClose"> </el-button>
      <el-button type="primary" @click="handleAddClose"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import bus from './BUS.vue'
  import { Loading } from 'element-ui';

  export default {
    data () {
      return {
        form:{
          wantedName:'',
          salary:'',
          major:'',
          jianli:'',
          address:'',
          intention:'',
          mailbox:'',
          phone:''
        },
        dialogFormAddSubjectVisible: false,
        isLast:false,
        isLast2:false
      }
    },
    watch:{
      "this.form": function (newValue) {
        console.log(newValue);
      }
    },
    methods:{
      handleAddClose(){  //
        this.dialogFormAddSubjectVisible = false;
        var loadingInstance = Loading.service({   //
          target:'#container',
          text:'...'
        });
        var params = new URLSearchParams();
        params.append('stuId',this.userEntity.id);
        params.append('wantedName',this.form.wantedName);
        params.append('address',this.form.address);
        params.append('intention',this.form.intention);
        params.append('major',this.form.major);
        params.append('salary',this.form.salary);
        params.append('jianli',this.form.jianli);
        params.append('mailbox',this.form.mailbox);
        params.append('phone',this.form.phone);
        this.axios.post('http://49.235.193.231:8700/wanted/updateWanted',params)
          .then( res => {
            loadingInstance.close();
            this.$message.success('');
          })
          .catch( res => {
            loadingInstance.close();
            this.$message.error('');
          })
        this.handleClose();
      },
      handleClose () {
        this.dialogFormAddSubjectVisible = false
      }
    },
    mounted() {




      //
      bus.$on('wantedEdit', data => {   //
        this.dialogFormAddSubjectVisible = data.flag;
        this.isLast = data.isLast;
        this.form = data.value;
      })
    }
  }
</script>

<style scoped>

</style>



