<template>
  <el-dialog title="" :visible.sync="dialogFormAddSubjectVisible"  :show-close="false">
    <el-form :model="form">
      <el-form-item label="">
        <el-input v-model="form.enterName" disabled></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.recruitName"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.introduction"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.major"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.address"></el-input>
      </el-form-item>
      <el-form-item label="/">
        <el-input v-model="form.salary"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.hrName"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.hrPhone"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.hrMailbox"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="handleClose"> </el-button>
      <el-button type="primary" @click="handleAddClose"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import bus from './BUS.vue'
  import { Loading } from 'element-ui';

  export default {
    data () {
      return {
        form:{
          recruitName:'',
          salary:'',
          enterName:'',
          major:'',
          hrName:'',
          address:'',
          introduction:'',
          hrMailbox:'',
          hrPhone:''
        },
        dialogFormAddSubjectVisible: false,
        isLast:false,
        isLast2:false
      }
    },
    watch:{
      "this.form": function (newValue) {
        console.log(newValue);
      }
    },
    methods:{
      handleAddClose(){  //
        this.dialogFormAddSubjectVisible = false;
        var loadingInstance = Loading.service({   //
          target:'#container',
          text:'...'
        });
        var params = new URLSearchParams();
        params.append('enterName',this.form.enterName);
        params.append('recruitName',this.form.recruitName);
        params.append('address',this.form.address);
        params.append('introduction',this.form.introduction);
        params.append('major',this.form.major);
        params.append('salary',this.form.salary);
        params.append('hrName',this.form.hrName);
        params.append('hrMailbox',this.form.hrMailbox);
        params.append('hrPhone',this.form.hrPhone);
        this.axios.post('http://49.235.193.231:8700/recruit/updateRecruit',params)
          .then( res => {
            loadingInstance.close();
            this.$message.success('');
          })
          .catch( res => {
            loadingInstance.close();
            this.$message.error('');
          })
        this.handleClose();
      },
      handleClose () {
        this.dialogFormAddSubjectVisible = false
      }
    },
    mounted() {




      //
      bus.$on('recruitEdit', data => {   //
        this.dialogFormAddSubjectVisible = data.flag;
        this.isLast = data.isLast;
        this.form = data.value;
      })
    }
  }
</script>

<style scoped>

</style>



