<template>
  <el-dialog title="" :visible.sync="dialogFormAddSubjectVisible"  :show-close="false">
    <el-form :model="form">
      <el-form-item label="">
        <el-input v-model="form.account"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.password"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.enterName"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.major"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.owner"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.address"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.introduction"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.mailbox"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.telephone"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="handleClose"> </el-button>
      <el-button type="primary" @click="handleAddClose"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import bus from './BUS.vue'
  import { Loading } from 'element-ui';

  export default {
    data () {
      return {
        form:{
          account:'',
          password:'',
          enterName:'',
          major:'',
          owner:'',
          address:'',
          introduction:'',
          mailbox:'',
          telephone:''
        },
        dialogFormAddSubjectVisible: false,
        isLast:false,
        isLast2:false
      }
    },
    watch:{
      "this.form": function (newValue) {
        console.log(newValue);
      }
    },
    methods:{
      handleAddClose(){  //
        this.dialogFormAddSubjectVisible = false;
        var loadingInstance = Loading.service({   //
          target:'#container',
          text:'...'
        });
        var params = new URLSearchParams();
        params.append('account',this.form.account);
        params.append('password',this.form.password);
        params.append('enterName',this.form.enterName);
        params.append('major',this.form.major);
        params.append('owner',this.form.owner);
        params.append('address',this.form.address);
        params.append('introduction',this.form.introduction);
        params.append('mailbox',this.form.mailbox);
        params.append('telephone',this.form.telephone);
        params.append('type',3);
        this.axios.post('http://49.235.193.231:8700/enterprise/addEnterprise',params)
          .then( res => {
            loadingInstance.close();
            this.$message.success('');
            bus.$emit("isLast");
          })
          .catch( res => {
            loadingInstance.close();
            this.$message.error('');
          })
        this.handleClose();
      },
      handleClose () {
        this.dialogFormAddSubjectVisible = false
      }
    },
    mounted() {




      //
      bus.$on('enterpriseAdd', data => {   //
        this.dialogFormAddSubjectVisible = data.flag;
        this.isLast = data.isLast;
      })
    }
  }
</script>

<style scoped>

</style>




