<template>
  <el-dialog title="" :visible.sync="dialogFormAddSubjectVisible"  :show-close="false">
    <el-form :model="form">
      <el-form-item label="">
        <el-input v-model="form.stuName" disabled></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.enterName" disabled></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.address" disabled></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.salary" disabled></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.contractTime" disabled></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="handleClose"> </el-button>
      <el-button type="primary" @click="handleAddClose"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import bus from './BUS.vue'
  import { Loading } from 'element-ui';

  export default {
    data () {
      return {
        form:{
          stuName:'',
          salary:'',
          address:'',
          enterName:'',
          contractTime:''
        },
        dialogFormAddSubjectVisible: false,
        isLast:false,
        isLast2:false
      }
    },
    watch:{
      "this.form": function (newValue) {
        console.log(newValue);
      }
    },
    methods:{
      handleAddClose(){  //
        this.dialogFormAddSubjectVisible = false;
        var loadingInstance = Loading.service({   //
          target:'#container',
          text:'...'
        });
        var params = new URLSearchParams();
        params.append('address',this.form.address);
        params.append('salary',this.form.salary);
        params.append('contractTime',this.form.contractTime);
        this.axios.post('http://49.235.193.231:8700/employ/updateEmploy',params)
          .then( res => {
            loadingInstance.close();
            this.$message.success('');
          })
          .catch( res => {
            loadingInstance.close();
            this.$message.error('');
          })
        this.handleClose();
      },
      handleClose () {
        this.dialogFormAddSubjectVisible = false
      }
    },
    mounted() {




      //
      bus.$on('employEdit', data => {   //
        this.dialogFormAddSubjectVisible = data.flag;
        this.isLast = data.isLast;
        this.form = data.value;
      })
    }
  }
</script>

<style scoped>

</style>



