<template>
  <el-dialog title="" :visible.sync="dialogFormAddSubjectVisible"  :show-close="false">
    <el-form :model="form">
      <el-form-item label="">
        <el-input v-model="form.stuId"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.account"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.address"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-input v-model="form.salary"></el-input>
      </el-form-item>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="handleClose"> </el-button>
      <el-button type="primary" @click="handleAddClose"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
  import bus from './BUS.vue'
  import { Loading } from 'element-ui';

  export default {
    data () {
      return {
        form:{
          stuId:'',
          account:'',
          address:'',
          salary:'',
          contractTime:''
        },
        dialogFormAddSubjectVisible: false,
        isLast:false,
        isLast2:false
      }
    },
    watch:{
      "this.form": function (newValue) {
        console.log(newValue);
      }
    },
    methods:{
      handleAddClose(){  //
        var myDate = new Date();
        myDate.getFullYear();    //(4,1970-????)
        myDate.getMonth();       //(0-11,01)
        myDate.getDate();        //(1-31)
        myDate.getHours();       //(0-23)
        myDate.getMinutes();     //(0-59)
        this.dialogFormAddSubjectVisible = false;
        var loadingInstance = Loading.service({   //
          target:'#container',
          text:'...'
        });
        var params = new URLSearchParams();
        params.append('stuId',this.form.stuId);
        params.append('enterAccount',this.form.account);
        params.append('address',this.form.address);
        params.append('salary',this.form.salary);
        params.append('createTime',myDate);
        this.axios.post('http://49.235.193.231:8700/employ/addEmploy',params)
          .then( res => {
            loadingInstance.close();
            this.$message.success('');
            bus.$emit("isLast");
          })
          .catch( res => {
            loadingInstance.close();
            this.$message.error('');
          })
        this.handleClose();
      },
      handleClose () {
        this.dialogFormAddSubjectVisible = false
      }
    },
    mounted() {




      //
      bus.$on('employAdd', data => {   //
        this.dialogFormAddSubjectVisible = data.flag;
        this.isLast = data.isLast;
      })
    }
  }
</script>

<style scoped>

</style>




